li{
  list-style-type: none;
}

.container{
  padding-left: 62px;
  padding-right: 62px;
}
.navbar{
  margin-bottom: 0px;
  min-height: 0px;
  font-size: 15px;
  font-weight: normal;
  border-radius: 0px;
  border-top: 0px;
  border-right: 0px;
}

#lang-dropdown,#curr-dropdown{
  padding-bottom: 5px;
  padding-top: 5px;

}
.navbar-default .navbar-nav>.active>a{
    background-color: #fff;
}
#navbar-form{
  padding-left: 20px;
}

#search-input {
  width:300px;
}
#mid-nav{
}

@media (min-width: 767px){
  #top-cart,#top-account{
      display: none;
      padding-top: 0px;
  }
}

@media (max-width: 767px){
  #mid-nav{
    display: none;
  }
}
@media (max-width: 992px){
  #navbar-form{
    padding-left:50px;
  }
}
@media (max-width: 767px){
  #navbar-form{
    float: right;
    margin-right: 15px;
  }
  #search-input{
    padding-left: 10px;
    width: 400px;
  }
}
@media (max-width: 597px){
  #navbar-form{
    padding-left: 20px;
  }
  #search-input{
    width: 150px;
  }
}
.navbar-form{
  border: 0px;
  display: -webkit-box;
  margin-top: 0px;
  margin-bottom: 0px;
  padding: 10px 0px 10px 10px;
}

#submit-btn{
  margin-left: 2px;
}

.center-nav {
  width: 100%;
  text-align: center;
}
.center-nav>li {
  float: initial;
  line-height: 150px;
}
.center-nav>li {
    display:inline-block;
}
.items{
  float: left;
  margin: 0 0 0 1px;
  padding: 6px 0 10px 10px;
  width: 100px;
}
.dropdown-menu{
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  left: -200px;
}
dt{
  border: dashed #cacaca;
  border-width: 0 0 1px;
  color: #202020;
  /* font-family: Georgia; */
  /* font-size: 1.05em; */
  /* line-height: 2.0em; */
  /* font-weight: normal; */
  letter-spacing: 0.5px;
  padding-left: 10px;
  padding-right: 0;
  text-transform: uppercase;
  width: auto;
}
dd{
  height: 90px;
}
#section{
    border: solid #999;
    border-width: 0 0 0 1px;
    display: inline;
    float: left;
    margin-bottom: -10000px;
    overflow: hidden;
    padding-bottom: 10000px;
}

.inner {
  width: 50%;
  margin: 0 auto;
}
.slick-next:before, .slick-prev:before{
  color: #000;
}
.slick-slide img {
    display: block;
    width: 100%;

}



